
.form-container {
  background-color:black;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    
   
  }
  
  .register-form {
    background-color: #ffffff;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
  }
  .register-form h3 {
    margin-bottom: 20px;
}
.register-form input[type="text"], .register-form input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid black;
  border-radius: 5px;
  outline: none;
  box-sizing: border-box;
}
.register-form input[type="email"], .register-form input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid black;
  border-radius: 5px;
  outline: none;
  box-sizing: border-box;
}
.register-form input[type="submit"] {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}
.register-form input[type="submit"]:hover {
  background-color: #0056b3;
}
.register-link {
  display: block;
  text-align: center;
  margin-top: 20px;
  color: #333;
  text-decoration: none;
}
.register-link:hover {
  color: #007bff;
}
